export const getSexByName = {
  methods: {
    getSexByName (type) {
      if (type === 1) {
        return '男'
      } else {
        return '女'
      }
    }
  }
}

export const getRelation = {
  methods: {
    getRelation (type) {
      switch (type) {
        case 0:
          return '父亲'
          break
        case 1:
          return '母亲'
          break
        case 2:
          return '儿子'
          break
        case 3:
          return '女儿'
          break
        case 4:
          return '妻子'
          break
        case 5:
          return '丈夫'
          break
        case 6:
          return '本人'
          break
        default:
          return "本人"
      }
    }
  }
}
